* {
    box-sizing: border-box;
}

body {
    background-color: rgb(140, 82, 159);
}

.container {
    width: calc(100vw - 30vw);
    margin: 5% auto;
    /* border: 2px solid rgb(203, 198, 198); */
}

.app-name {
    text-align: center;
    font-family: 'Laila', sans-serif;
    letter-spacing: 0.5px;
    color: #c09b56;
    font-size: calc(100vw - 97vw);
}

.add-contact-section {
    width: 70%;
    margin: 5% auto
}

h2 {
    font-family: 'Oswald', sans-serif;
    color: #eeeeeeb4;
    font-size: calc(100vw - 97.7vw);
}

.form {
    width: 95%;
    margin: 1.5% auto;
}

.field {
    width: 100%;
}

.field label {
    font-size: calc(100vw - 98vw);
    /* font-family: 'Kanit', sans-serif; */
    font-family: 'Laila', sans-serif;
    /* font-family: 'Oswald', sans-serif; */
    color: #212121
}

.user-info-input {
    width: 100%;
    height: calc(100vh - 93vh);
    border-radius: 5px;
    font-size: large;
    padding-left: 1%;
    margin-top: -0.9%;
    background-color: #d5d5d5;
}

.add-contact-button {
    display: block;
    width: calc(100vw - 93vw);
    height: calc(100vh - 91.5vh);
    margin-top: 2%;
    background-color: rgba(84, 204, 128, 0.715);
    border-radius: 15px;
    font-family: 'Kanit', sans-serif;
    font-size: calc(100vw - 98.5vw);
    font-weight: 700;
    cursor: pointer;
}

.add-contact-button-in-first-page {
    display: block;
    width: calc(100vw - 87vw);
    height: calc(100vh - 90vh);
    margin: 0 auto;
    margin-top: 2%;
    background-color: rgba(84, 204, 128, 0.715);
    border-radius: 15px;
    font-family: 'Kanit', sans-serif;
    font-size: calc(100vw - 98vw);
    font-weight: 700;
    text-decoration: none;
    cursor: pointer;
}

.contact-list {
    width: 70%;
    margin: 0 auto
}

.contact-card {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    justify-content: space-between;
    width: 70%;
    height: calc(100vh - 91vh);
    /* background-color: red; */
    margin: 5% auto;
}

.content {
    display: flex;
    align-items: center;
    justify-content: safe;
}

.user-image {
    object-fit: cover;
    width: calc(100vw - 95vw);
    height: calc(100vh - 92vh);
    border-radius: 50%;
}

.contact-name {
    font-family: 'Oswald', sans-serif;
    margin-left: 5%;
    font-size: calc(100vw - 98vw);
}

.fa-trash {
    color: rgb(144, 24, 24);
    cursor: pointer;
}

a {
    text-decoration: none;
    color: inherit;
    cursor: pointer;
}

a:visited {
    color: inherit;
    cursor: pointer;
}

.contact-detail-container {
    width: calc(100vw - 50vw);
    margin: 0 auto;
    /* background-color: red; */
}

.contact-detail-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-evenly;
    align-items: center;
}

.detail-label {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: wrap;
}

.header {
    font-family: 'Ysabeau', sans-serif;
    font-size: calc(100vw - 97vw);
    font-weight: 600;
    color: rgb(54, 41, 28);
}

.content-of-header {
    font-family: 'Fira Sans', sans-serif;
    font-size: calc(100vw - 97vw);
    color: rgb(161, 154, 147);
}

.home-button {
    width: calc(100vw - 90vw);
    height: calc(100vh - 92vh);
    margin-top: 10%;
    font-size: calc(100vw - 98.6vw);
    border-radius: 20px;
    background-color: rgba(84, 204, 128, 0.715);
    cursor: pointer;
}

.image-field {
    width: 100%;
    height: calc(100vh - 75vh);
}

.image-input {
    width: calc(100vw - 90vw);
    height: calc(100vh - 83vh);
    border-radius: 50%;
    margin: 0 auto;
    display: block;
    border: 0;
    background-color: #d5d5d5;
    object-fit: cover;
    text-align: center;
}

input[type="file"] {
    display: none;
}

.upload-button {
    margin: 2% auto;
    display: block;
    width: calc(100vw - 90vw);
    height: calc(100vh - 94vh);
    background-color: purple;
    color: #d5d5d5;
    font-size: large;
    border-radius: 20px;
    font-weight: 900;
    border: 0;
    font-family: 'Ysabeau', sans-serif;
    cursor: pointer;
    text-align: center;
    padding-top: 1.5%;
}